*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.mainAddingDeveloper{
    width: 100%;
    /* min-height: 100vh; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: #fff; */
    padding: 2rem;
    margin-top: 1%;
}
.innerAddingDeveloper{
    width: 90%;
    display: flex;
    /* min-height: 70vh; */
    /* padding-top: 5%; */
    /* padding-bottom: 5%; */
    box-shadow: 0 2px 8px 2px rgba(0,0,0,0.2);;
    background: #fff;
}
.addingDeveloperHeadings{
    width: 35%;
    background-color: #02044A;
    /* background-color: #269b6a; */
    padding: 2rem;
    margin: 0.5rem;
    border-top-right-radius: 35px;
    border-bottom-right-radius: 35px;
    position: relative;
    overflow: hidden;
    z-index: 1;
}
.inputForm{
    width: 65%;
    background-color: #fff;
}
.model3d{
    position: absolute;
    width: 60%;
    bottom: 0;
    z-index: -1;
    left: 50%;
}
.techImage{
    position: absolute;
    width: 15%;
}
.html{
    bottom: 0;
}
.css{
    bottom: 50px;
    left: 30%;
}
.javascripts{
    bottom: 12%;
    left: 2%;
}
.reacts{
    bottom: 18%;
    left: 30%;
}
.angular{
    bottom: 22%;
}
.nodejs{
    bottom: 0;
    left: 40%;
}
.mongodb{
    bottom: 25%;
    left: 40%;
}
.addingDeveloperHeadings h1{
    font-family: 'Josefin Sans';
    color: #fff;
    font-size: 2.2rem;
    font-weight: bold;
}

.addingDeveloperHeadings p{
    font-family: 'Poppins';
    color: #fad647;
    font-size: 1rem;
}
.pointsToRemember{
    margin-top: 8%;
}
.pointsToRemember ul{
    list-style: circle;
}
.pointsToRemember  h2{
    color: #fff;
      font-family: 'Josefin Sans';
      font-size: 1.5rem;
}
.pointsToRemember ul li{
    font-family: 'Poppins';
    color: #fad647;
    margin: 1.5rem 0;
}

.inputForm{
    padding: 2rem 0 2rem 2rem;
}
.inputField1{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5%;
}
.developerName,.developerDesignation{
    width: 50%;
}
.developerName h4{
    font-family: 'Josefin Sans';
    font-size: 1.2rem;
}
.developerName input{
    width: 95%;
    height: 40px;
    padding-left: 10px;
    font-family: 'Poppins';
    border: 1px solid #f1f1f1;
}
#browsers{
    font-size: 2rem;
}
.developerName datalist option{
    font-family: 'Poppins' !important;
    font-size: 2rem;
}
.developerDesignation h4{
    font-family: 'Josefin Sans';
    font-size: 1.2rem;
}
.developerDesignation input{
    width: 95%;
    height: 40px;
    padding-left: 10px;
    font-family: 'Poppins';
    border: 1px solid #f1f1f1;
}
#fileInput{
    padding-top: 5px;
}
.MuiFormGroup-root{
    flex-direction: row !important;
}
.MuiRadio-colorSecondary.Mui-checked{
    color: #0d5179 !important;
}
.MuiRadio-colorSecondary:hover{
    background-color: none !important;
}
.MuiFormControlLabel-root{
    margin-right: 26px;
    /* width: 100% !important; */
}
.PrivateSwitchBase-root-1{
    background-color: #fff !important;
}
.MuiTypography-body1{
    font-family: 'Poppins' !important;
}
.MuiFormLabel-root{
    font-family: 'Josefin Sans' !important;
    color: #000 !important;
    font-size: 1.3rem !important;
    font-weight: bold !important;
}


.priceRange{
    margin-top: 6%;
}

.availabilityArea{
    margin-top: 6%;
}



.submitButton{
    width: 90%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 6%;
}
.submitButton button{
    width: 22%;
    height: 50px;
    outline: none;
    border: none;
    border-radius: 25px;
    /* background: #1475ff; */
    background: #02044A;
    color: #fff;
    font-family: 'Poppins';
    font-size: 20px;
    box-shadow: 0 2px 8px 2px rgba(0,0,0,0.2);
}